html {
  font-size: 12px;
  font-weight: 500;
}

body {
  --active-color: #00acdc;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo_link {
  width: 40px;
  height: 40px;
}

.list__items {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.list__item:not(:last-child) {
  margin-right: 20px;
}

.nav__link {
  color: #000;
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav__link:hover, .is-active {
  color: var(--active-color);
}

.is-active:after {
  content: "";
  background-color: var(--active-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

